.header__logo {
    width: 100%;
    max-width: 142px;
}

@media all and (max-width: 768px) {
  .header__logo {
    max-width: 120px;
  }
}

@media all and (max-width: 425px) {
  .header__logo {
    max-width: 103px;
    width: calc(100% - 2 * 27px);
    margin: 28px auto auto 27px;
  }
}

