.element {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas:
  'element__foto element__foto element__foto'
  'element__title element__title element__like';
  border-radius: 10px;
  background-color: white;
  color: black;
  min-height: 361px;
}
